import React, { Component } from 'react';
import OneStyle from './One.module.scss';
import ChartHeader from '../../../../components/ChartHeader/ChartHeader';
import ReactEcharts from 'echarts-for-react';
import * as echarts from 'echarts'; // echarts 

export default class One extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Name: ['', '', '', '', '', '', '', ''],
            Data: [68, 88, 64, 78, 70, 62, 56, 53],
        }
    }

    getOption = (data, name) => {
        return {
            tooltip: {
                formatter: '{b} : {c}',
                backgroundColor: 'rgba(255,255,255,0.8)',
                textStyle: {
                    color: '#000000'
                }
            },
            grid: {
                top: '22%',
                left: '7%',
                right: '1%',
                bottom: '15%',
            },
            color: '#0091FF',
            xAxis: {
                type: 'category',
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: true,
                    textStyle: {
                        color: "rgba(237,237,237,1)",
                        fontSize: 11,
                    },
                    interval: 0,
                    formatter: function (value) {
                        var ret = "";//\n  
                        var maxLength = 3;//  
                        var valLength = value.length;//X  
                        var rowN = Math.ceil(valLength / maxLength); //  
                        if (rowN > 1)//3,  
                        {
                            for (var i = 0; i < rowN; i++) {
                                var temp = "";//  
                                var start = i * maxLength;//  
                                var end = start + maxLength;//  
                                //  
                                temp = value.substring(start, end) + "\n";
                                ret += temp; //  
                            }
                            return ret;
                        }
                        else {
                            return value;
                        }
                    }
                },
                data: name,
            },
            yAxis: {
                type: 'value',
                max: 88,
                min: 0,
                interval: 22, //
                axisLine: {
                    show: false
                },
                axisLabel: {
                    show: true,
                    textStyle: {
                        color: "rgba(237,237,237,1)",
                        fontSize: 11,
                    },
                },
                axisTick: {
                    show: false
                },
                show: true,
                splitLine: {
                    show: true,
                    lineStyle: {
                        color: '#134C78',
                        type: 'dashed'//
                    }
                }
            },
            series: [
                {
                    type: 'bar',
                    barWidth: 13,//
                    data: data,
                    itemStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(
                                0, 0, 0, 1,
                                [
                                    { offset: 1, color: '#008CFF' },                   //
                                    { offset: 0, color: '#15F2FF' },                   //
                                ]
                            )
                        },
                    }
                }
            ]
        }
    };


    render() {
        return (
            <div className={OneStyle.fiveMain}>
                <ChartHeader title='' />
                <div className={OneStyle.chart}>
                    <ReactEcharts style={{ width: '100%', height: '100%' }} option={this.getOption(this.state.Data, this.state.Name)} />
                </div>
            </div>
        );
    }
}
